import type { IMessageReceived, IMessageSent } from "./types";
import { config } from "./config";
import { Blockchain } from "./methods";

const blockhcain = new Blockchain();

const sendMessage = (message: IMessageSent) => {
  const strMessage = JSON.stringify(message);
  window.parent.postMessage(strMessage, "*");
};

const receiveMessage = async (event: MessageEvent) => {
  // if (!config.allowedOrigins.includes(event.origin)) return;
  const req: IMessageReceived = JSON.parse(event.data);
  const { data, method } = req;

  if (method === "initiate_wallet") {
    try {
      await blockhcain.setupWallet(data.jwt);
      sendMessage({
        data: {
          message: "Wallet initialized",
        },
        method: method,
        type: "response",
        error: null,
      });
    } catch (error) {
      sendMessage({
        data: null,
        method: method,
        type: "response",
        error: error.message,
      });
    }
  } else if (method === "get_profile") {
    const profile = await blockhcain.getProfile();
    sendMessage({
      data: profile,
      method: method,
      type: "response",
      error: null,
    });
  } else if (method === "set_profile") {
    try {
      const { key, value } = data;
      await blockhcain.setProfile(key, value);
      sendMessage({
        data: {
          message: "Profile updated",
        },
        method: method,
        type: "response",
        error: null,
      });
    } catch (error) {
      console.log(error);
      sendMessage({
        data: null,
        method: method,
        type: "response",
        error: "Something went wrong",
      });
    }
  } else {
    sendMessage({
      data: null,
      method: method,
      type: "response",
      error: "Invalid method",
    });
  }
};

window.addEventListener("message", receiveMessage, false);

window.onload = () => {
  sendMessage({
    type: "event",
    method: "wallet_launched",
    error: null,
    data: {},
  });
};

/*
 * Available functions:
 * NAME                |     PARAMS
 *
 * initiate_wallet     |     { jwt: string }
 * get_profile         |     null
 * set_profile         |     { key: string; value: string }
 *
 */
